.searchForm {
    margin-bottom: 30px;
  
    display: flex;
    align-items: center;
    width: 100%;
    height: 41px;
    background: #272727;
    box-shadow: 0px 2px 12px 7px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
    overflow: hidden;
  }
  
  .searchForm_button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-image: url('images/Vector.png');
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  .searchForm_button:hover {
    opacity: 1;
  }
  
  .searchForm_button_label {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
  }
  
  .searchForm_input {
    width: 100%;
  
    appearance: none;
    border: none;
    background-color: transparent;
    box-shadow: none;
    outline: none;
  
    font-size: 20px;
    padding-left: 10px;
    padding-right: 10px;
  
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    line-height: 1.5;
    color: #707070;
  }